const mySwiper = new Swiper('.our-record__slider', {
  // Optional parameters
  loop: true,
  slidesPerView: 6,
  centeredSlides: true,
  spaceBetween: 23,
  grabCursor: true,
  // Navigation arrows
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
})

const swiperPrev = document.querySelector('.our-record__arrows .main-arrows__arrow--left');
const swiperNext = document.querySelector('.our-record__arrows .main-arrows__arrow--right');

swiperPrev.addEventListener('click', () => {
  mySwiper.slidePrev();
})
swiperNext.addEventListener('click', () => {
  mySwiper.slideNext();
})
